.formats-section {
  &__container {
    display: flex;
    gap: 32px;
    align-items: center;
  }

  &__title {
    margin-bottom: 80px;
  }

  &__list {
    display: flex;
    gap: 20px;
    flex-wrap: wrap;
  }

  &__item {
    flex-basis: calc(
      (100% - 40px) / 3
    ); /*for items inside flex-container, 3 items in a row + (2 paddings * 2)*/
  }
}
